<template>
  <div class="mt-4">
    <v-card-title>
      <div class="text-h5 font-weight-light">
        Administrasi Pinjaman
      </div>
      <JsonExcel v-if="data.length > 0" :data="data" :fields="json_fields" class="ml-auto" x-small>
        <v-btn class="primary"><v-icon color="white">mdi-download</v-icon> <span class="hidden-sm-and-down">Download</span></v-btn>
      </JsonExcel>
    </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-row class="mb-4">
        <v-col cols="12" md="2" xs="12">
          <v-text-field v-model="form.employeeID" @focusout="reqData()" append-icon="mdi-magnify" label="NIK"></v-text-field>
        </v-col>
        <v-col cols="6" md="3" xs="6">
          <v-dialog
            ref="dialog_from"
            v-model="modal_from"
            :return-value.sync="form.from_month"
            persistent
            width="290px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="form.from_month"
                label="Dari Bulan"
                append-icon="mdi-calendar"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker
              v-model="form.from_month"
              type="month"
              scrollable
            >
              <v-spacer></v-spacer>
              <v-btn
                text
                color="primary"
                @click="modal_from = false"
              >
                Cancel
              </v-btn>
              <v-btn
                text
                color="primary"
                @click="saveFromMonth(form.from_month)"
              >
                OK
              </v-btn>
            </v-date-picker>
          </v-dialog>
        </v-col>
        <v-col cols="6" md="3" xs="6">
          <v-dialog
            ref="dialog_to"
            v-model="modal_to"
            :return-value.sync="form.to_month"
            persistent
            width="290px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="form.to_month"
                label="Sampai Bulan"
                append-icon="mdi-calendar"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker
              v-model="form.to_month"
              type="month"
              scrollable
            >
              <v-spacer></v-spacer>
              <v-btn
                text
                color="primary"
                @click="modal_to = false"
              >
                Cancel
              </v-btn>
              <v-btn
                text
                color="primary"
                @click="saveToMonth(form.to_month)"
              >
                OK
              </v-btn>
            </v-date-picker>
          </v-dialog>
        </v-col>
        <v-col cols="6" md="2" xs="6">
          <v-select
            :items="statuses"
            item-text="text"
            item-value="value"
            label="Status"
            v-model="form.status"
            @change="reqData()"
          ></v-select>
        </v-col>
        <v-col cols="6" md="2" xs="6">
          <v-select
            :items="approvals"
            label="Next Approval"
            v-model="form.selectApproval"
            @change="reqData()"
          ></v-select>
        </v-col>
      </v-row>
      <v-data-table :headers="headers" :items="data" :fields="json_fields" loading-text="Loading... Please wait" class="elevation-1" :loading="loading"
        :search="search">
        <template #[`item.index`]="{ item }">
          {{ data.indexOf(item) + 1 }}.
        </template>
        <template #[`item.SubmitDate`]="{ item }">
            {{ item.SubmitDate ? toDateTime(item.SubmitDate) : item.SubmitDate }}
        </template>
        <template #[`item.firstname`]="{ item }">
            {{ item.firstname }} {{ item.lastname }}
        </template>
        <template #[`item.HC_Recommendations`]="{ item }">
            {{ yesOrNo(item.HC_Recommendations) }}
        </template>
        <template #[`item.DSR`]="{ item }">
            {{ yesOrNo(item.DSR) }}
        </template>
        <template #[`item.SP`]="{ item }">
            {{ yesOrNo(item.SP) }}
        </template>
        <template #[`item.LoanAmount`]="{ item }">
            {{ item.LoanAmount > 0 ? toRupiah(item.LoanAmount) : '0'}}
        </template>
        <template #[`item.Angsuran`]="{ item }">
            {{ item.Angsuran > 0 ? toRupiah(item.Angsuran) : '0'}}
        </template>
        <template #[`item.TotalPencairan`]="{ item }">
            {{ item.TotalPencairan > 0 ? toRupiah(item.TotalPencairan) : '0'}}
        </template>
        <template #[`item.ApvSekretaris`]="{ item }">
            {{ item.ApvSekretaris ? 'Disetujui' : '' }}
        </template>
        <template #[`item.ApvKetua`]="{ item }">
            {{ item.ApvKetua ? 'Disetujui' : '' }}
        </template>
        <template #[`item.Status`]="{ item }">
          <v-chip small
            class="ma-2"
            :color="getColorStatus(item.Status)"
            text-color="white"
          >
            {{ item.Status }}
          </v-chip>
        </template>
        <template #[`item.action`]="{ item }">
          <div>
            <v-btn v-if="item.NextApproval=='LoanAdmin' && item.Status !='CNCL' && item.Status !='RJT'" icon color="success" @click="edit(item)">
              <v-icon>
                mdi-square-edit-outline
              </v-icon>
            </v-btn>
            <v-btn v-else icon color="warning" @click="detail(item)">
              <v-icon>
                mdi-file-find-outline
              </v-icon>
            </v-btn>
            <v-btn :disabled="checkStatus(item.Status, item.StatusPencairan)" color="primary" @click="cairkan(item)">
              Cairkan
            </v-btn>
          </div>
        </template>
      </v-data-table>
    </v-card>
    <detail/>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import detail from './../components/Loan/Detail.vue'
import store from '@/store'
import Utils from '@/utils/util'
import JsonExcel from 'vue-json-excel'
export default {
  name: 'LoanPage',
  components: {
    detail, JsonExcel
  },
  data: () => ({
    search: '',
    headers: [
      {
        text: '#',
        align: 'start',
        sortable: false,
        value: 'index'
      },
      {
        text: 'Tgl Pengajuan',
        sortable: false,
        value: 'SubmitDate',
        width: '150px'
      },
      {
        text: 'No Approval',
        sortable: false,
        value: 'ApprovalNo'
      },
      { text: 'NIK', value: 'NIK', sortable: false },
      { text: 'Nama', value: 'firstname', sortable: false },
      { text: 'Company', value: 'id_company', sortable: false },
      { text: 'Jenis Pengajuan', value: 'JenisPengajuan', sortable: false },
      { text: 'Pinjaman', value: 'LoanAmount', sortable: false, align: 'right' },
      { text: 'Angsuran', value: 'Angsuran', sortable: false, align: 'right' },
      { text: 'Total Pencairan', value: 'TotalPencairan', sortable: false, align: 'right' },
      { text: 'Rekomendasi', value: 'HC_Recommendations', sortable: false },
      { text: 'DSR', value: 'DSR', sortable: false },
      { text: 'SP', value: 'SP', sortable: false },
      { text: 'Next Approval', value: 'NextApproval', sortable: false },
      { text: 'Status', value: 'Status', sortable: false, align: 'center' },
      { text: 'Process By', value: 'ProcessBy', sortable: false },
      { text: 'Action', value: 'action', sortable: false, width: '170px' }
    ],
    // form: {
    //   employeeID: '',
    //   from_month: new Date().toISOString().substr(0, 7),
    //   to_month: new Date().toISOString().substr(0, 7),
    //   selectApproval: '',
    //   status: ''
    // },
    statuses: [
      { value: '', text: 'Semua' },
      { value: 'NEW', text: 'New' },
      { value: 'APV', text: 'Approve' },
      { value: 'FUN', text: 'Funding' },
      { value: 'CNCL', text: 'Cancel' },
      { value: 'RJT', text: 'Reject' }
    ],
    approvals: [
      { value: '', text: 'Semua' },
      { value: 'Sekretaris', text: 'Sekretaris' },
      { value: 'Bendahara', text: 'Bendahara' },
      { value: 'LoanAdmin', text: 'LoanAdmin' },
      { value: 'HCComben', text: 'HCComben' },
      { value: 'HC', text: 'HC' },
      { value: 'HCBP', text: 'HCBP' }
    ],
    modal_from: false,
    modal_to: false,
    json_fields: {
      'Tgl Pengajuan': 'SubmitDate',
      ApprovalNo: 'ApprovalNo',
      NIK: 'NIK',
      Nama: 'firstname',
      Company: 'id_company',
      'Lokasi Kerja': 'WorkLocation',
      'Jenis Pengajuan': 'JenisPengajuan',
      Pinjaman: 'LoanAmount',
      Angsuran: 'Angsuran',
      Tenor: 'Tenor',
      'Total Pencairan': 'TotalPencairan',
      'HC Recommendations': {
        field: 'HC_Recommendations',
        callback: (value) => {
          if (value === 0) {
            return 'Tidak'
          } else if (value === 1) {
            return 'Iya'
          } else {
            return ''
          }
        }
      },
      DSR: {
        field: 'DSR',
        callback: (value) => {
          if (value === 0) {
            return 'Tidak'
          } else if (value === 1) {
            return 'Iya'
          } else {
            return ''
          }
        }
      },
      SP: {
        field: 'SP',
        callback: (value) => {
          if (value === 0) {
            return 'Tidak'
          } else if (value === 1) {
            return 'Iya'
          } else {
            return ''
          }
        }
      },
      'Next Approval': 'NextApproval',
      Status: 'Status',
      'Process By': 'ProcessBy'
    }
  }),
  mounted: function () {
    store.dispatch('loan/reqData', store.getters['search/getSearch'])
  },
  computed: {
    ...mapState('loan', {
      loading: (state) => state.loading,
      data: (state) => state.data
    }),
    ...mapState('search', {
      form: (state) => state.form
    })
  },
  methods: {
    ...mapActions('loan', [
      'edit', 'detail', 'reqData', 'cairkan'
    ]),
    yesOrNo (item) {
      if (item === 1) {
        return 'Ya'
      } else if (item === 0) {
        return 'Tidak'
      } else {
        return '-'
      }
    },
    saveFromMonth (month) {
      this.$refs.dialog_from.save(month)
      this.reqData()
    },
    saveToMonth (month) {
      this.$refs.dialog_to.save(month)
      this.reqData()
    },
    toRupiah (nominal) {
      return Utils.toRupiahString(nominal)
    },
    toDateTime (tanggal) {
      return Utils.toDateStringMount(tanggal)
    },
    checkNextApproval (params) {
      if (params !== 'LoanAdmin') {
        return 'disabled'
      }
    },
    checkStatus (status, StatusPencairan) {
      if (status === 'APV' && StatusPencairan === '0') {
        return false
      }
      return true
    },
    getColorStatus (status) {
      if (status === 'NEW') {
        return 'success'
      } else if (status === 'CNCL' || status === 'RJT') {
        return 'red'
      } else if (status === 'APV') {
        return 'primary'
      } else {
        return 'orange'
      }
    }
  }
}
</script>
<style>
.v-btn.v-btn--is-elevated.v-btn--has-bg.theme--light.v-size--default.primary{
  background-color: blue !important;
}
</style>
