<script>
import { mapState, mapActions } from 'vuex'
import Utils from '@/utils/util'
// import VueNumeric from 'vue-numeric'
import VuetifyMoney from '../VuetifyMoney.vue'
import viewFIle from '@/components/Modal/ViewFile.vue'
// import axios from 'axios'
import axios from 'axios'

export default {
  name: 'LoanDetail',
  components: {
    VuetifyMoney, viewFIle
  },
  data () {
    return {
      pta: false,
      os: false,
      checked: true,
      notifications: false,
      sound: true,
      widgets: false,
      alignments: [
        'start',
        'center',
        'end'
      ],
      items: [
        {
          text: 'Ya', value: '1'
        },
        {
          text: 'Tidak', value: '0'
        }
      ],
      outlined: true,
      options: {
        locale: 'id-ID',
        prefix: 'Rp ',
        suffix: '',
        length: 12,
        precision: 0
      }
    }
  },
  inject: {
    theme: {
      default: { isDark: false }
    }
  },
  computed: {
    ...mapState('loan', {
      state: (state) => state
    }),
    checkedNST () {
      if (this.state.detail.JenisPengajuan === 'NTS') {
        return true
      }
      return false
    },
    checkedNonNST () {
      if (this.state.detail.JenisPengajuan === 'NORMAL') {
        return true
      }
      return false
    },
    select () {
      if (this.state.action === 'Detail') {
        return true
      }
      return false
    }
  },
  methods: {
    forceFileDownload (response, title) {
      console.log(title)
      const url = window.URL.createObjectURL(new Blob([response.data]))
      const link = document.createElement('a')
      link.href = url
      link.setAttribute('download', title)
      document.body.appendChild(link)
      link.click()
    },
    async downloadWithAxios (imageUrl) {
      // axios({
      //   method: 'get',
      //   imageUrl,
      //   headers: {
      //     'Content-Type': 'application/x-www-form-urlencoded'
      //   },
      //   responseType: 'arraybuffer',
      //   xhrFields: {
      //     withCredentials: true
      //   }
      // })
      //   .then((response) => {
      //     this.forceFileDownload(response, title)
      //   })
      //   .catch(() => console.log('error occured'))

      await axios({
        url: imageUrl,
        method: 'GET',
        responseType: 'blob',
        headers: {
          'Access-Control-Allow-Origin': '*'
        }
      }).then((response) => {
        const fileURL = window.URL.createObjectURL(new Blob([response.data]))
        const fileLink = document.createElement('a')

        fileLink.href = fileURL
        fileLink.setAttribute('download', 'file.png')
        document.body.appendChild(fileLink)

        fileLink.click()
      })

      // const response = await fetch(imageUrl, { mode: 'no-cors' })
      // const blobImage = await response.blob()
      // console.log(blobImage)
      // const href = URL.createObjectURL(blobImage)

      // const anchorElement = document.createElement('a')
      // anchorElement.href = href
      // anchorElement.download = 'file.png'

      // document.body.appendChild(anchorElement)
      // anchorElement.click()

      // document.body.removeChild(anchorElement)
      // window.URL.revokeObjectURL(href)
    },
    downloadFile (filename, blob) {
      const url = window.URL.createObjectURL(new Blob([blob]))
      const link = document.createElement('a')
      link.href = url
      link.setAttribute('download', filename)
      document.body.appendChild(link)
      link.click()
      link.remove()
    },
    ...mapActions('loan', [
      'simpan', 'prosesCairkan'
    ]),
    ...mapActions('modalFile', [
      'setFile'
    ]),
    toRupiah (nominal) {
      return 'Rp ' + Utils.toRupiahString(nominal)
    },
    toDateTime (tanggal) {
      return Utils.toDateTime(tanggal)
    },
    onFileChange (e) {
      const files = e.target.files || e.dataTransfer.files
      if (files[0].size < 1000000) {
        if (!files.length) return
        this.createImage(files[0])
      } else {
        this.$swal.fire(
          'Upload Dokumen Loan Admin',
          'File Terlalu Besar, Maks 1 Mb',
          'error'
        )
      }
    },
    createImage (file) {
      const reader = new FileReader()
      reader.onload = (e) => {
        this.state.detail.LoanAdminDoc = e.target.result
      }
      reader.readAsDataURL(file)
    },
    checkFile (file) {
      if (file) {
        const ext = file.split('.')
        if (ext[4] === 'pdf') {
          return true
        } else {
          return false
        }
      }
      return true
    }
  }
}
</script>
